import * as React from "react"
import { graphql } from "gatsby"
import { Container, Box, Kicker, Heading, Text } from "./ui"
import Feature from "./feature"

export default function FeatureList(props) {
  return (
    <Container id="Featurelist" width="fullbleed">
      <Box background="muted" radius="large" style={{ color: "white" }}>
        <Box center paddingY={3}>
          <Heading>
            {props.kicker && <Kicker>{props.kicker}</Kicker>}
            {props.heading}
          </Heading>
          {props.text && <Text>{props.text}</Text>}
        </Box>
        {props.content
          .filter((content) => content.showOnHomepage)
          .map((feature, i) => (
            <Feature key={feature.id} {...feature} flip={Boolean(i % 2)} />
          ))}
      </Box>
    </Container>
  )
}

export const query = graphql`
  fragment ArticleListContent on ArticleList {
    id
    kicker
    heading
    text
    content {
      id
      ...ArticleContent
    }
  }
`
