import * as React from "react"
import { graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { Container, Flex, Box, Subhead, Kicker, Text, Button } from "./ui"

export default function Feature(props) {
  const articleUrl =
    (props.articleType === "Insight" ? "/insights/" : "/case-studies/") +
    props.slug

  return (
    <Box paddingY={4} background="muted">
      <Container style={{ color: "white" }}>
        <Flex gap={4} variant="responsive">
          <Box width="half" order={props.flip ? 1 : null}>
            {props.image && (
              <GatsbyImage
                alt={props.image.alt || ""}
                image={getImage(props.image.gatsbyImageData)}
                style={{ height: "350px" }}
              />
            )}
          </Box>
          <Box width="half">
            <Subhead>
              <Kicker>{props.articleType}</Kicker>
              {props.heading}
            </Subhead>
            <Text variant="lead">{props.text}</Text>
            <Button href={articleUrl} variant={"primary"}>
              Learn More
            </Button>
          </Box>
        </Flex>
      </Container>
    </Box>
  )
}

export const query = graphql`
  fragment ArticleContent on Article {
    slug
    articleType
    showOnHomepage
    id
    kicker
    heading
    text
    links {
      id
      href
      text
    }
    image {
      id
      gatsbyImageData
      alt
    }
  }
`
