import * as React from "react"
import { graphql } from "gatsby"
import {
  Container,
  Section,
  FlexList,
  Text,
  Kicker,
  Heading,
  Subhead,
  Box,
  Icon,
  LinkList,
} from "./ui"

function Product(props) {
  return (
    <Box center>
        <Icon
          alt={props.image.alt}
          image={props.image}
          size="extraLarge"
        />
      <Subhead>{props.heading}</Subhead>
      <Text>{props.text}</Text>
      <LinkList links={props.links} />
    </Box>
  )
}

export default function ProductList(props) {
  return (
    <Section id="ProductList" paddingY={1}>
      <Container >
        <Box center paddingY={1}>
          <Heading>
            {props.kicker && <Kicker>{props.kicker}</Kicker>}
            {props.heading}
          </Heading>
          {props.text && <Text>{props.text}</Text>}
        </Box>
        <FlexList gap={4} variant="responsive" alignItems="start">
          {props.content.map((product) => (
            <Box key={product.id} width="third">
            <li key={product.id} id={"Product" + product.id}>
              <Product {...product} />
            </li>
            </Box>
          ))}
        </FlexList>
      </Container>
    </Section>
  )
}

export const query = graphql`
  fragment HomepageProductListContent on HomepageProductList {
    id
    kicker
    heading
    text
    content {
      id
      heading
      text
      image {
        alt
        id
        gatsbyImageData
        url
        mimeType
      }
      links {
        id
        href
        text
      }
    }
  }
`
